body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-inverse .navbar-nav .nav-link {
    color: #ffffff;
}
.display-3{
    font-size: 3rem;
    font-weight: 500;
    line-height: 1.1;
    color: #fff;
}

.carbonads {
    float: right;
    max-width: 330px;
    margin: -110px auto 0;
    border-width: 1px;
    border-radius: 4px;
    border-color: #fff;
    border-radius: .25rem;
    border: 1px solid #ccc;
    font-size: .875rem;
    overflow: hidden;
    padding: 1rem;
}

.jumbotron {
    background-color: #007bff!important;
}

.jumbotron p{
    color: #fff;
}

.input-group-addon {
    justify-content: center;
    min-width: 6rem;
}

.input-group {
    margin-bottom: 10px;
}
.input-group-addon-toggle {
    justify-content: center;
    min-width: 5rem;
    padding: .5rem .75rem;
    margin-bottom: 0;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.25;
    color: #464a4c;
    text-align: center;
    background-color: #eceeef;
    border: 1px solid rgba(0,0,0,.15);
    border-radius: .25rem;
}
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 60px;
    line-height: 60px;
    background-color: #292b2c!important;
}

.text-muted{
    color: #eeeeee !important;
}
